.container {
display: flex;
width: 250px;
flex-wrap: wrap;
padding: 10px;
}
.current-page {
    font-size: 1.5rem;
    vertical-align: middle;
  }
  
  ul.pagination {
    margin-top: 0;
    margin-bottom: 0;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    display: flex;
    list-style: none;
  }
  ul.pagination li.page-item.active a.page-link {
    color: #445565;
    background-color: #e3e7eb;
    border-color: #ced4da;
  }
  ul.pagination a.page-link {
    padding: 0.75rem 1rem;
    min-width: 3.5rem;
    text-align: center;
    box-shadow: none;
    border-color: #ced4da;
    color: #6b88a4;
    font-weight: 900;
    font-size: 1rem;
    text-decoration: none;
  }
  ul.pagination a.page-link:hover {
    background-color: #f0f0f0;
  }
  
